@import "../../../../styles/breakpoints.scss";

.host {
    display: flex;
    flex-direction: column;    
    padding: 0 8% 0 8%;
}
.labeledContainerHost {
    width: 100%;
}
.labeledContainerLabel {
    font-family: var(--app-text-main-font);
    font-size: 24px;
    color: #4a4a4a;
}
.avatarContainer {
}
.avatarDragContainer {
    cursor: pointer;
    
    flex: 1;
    border: solid silver 1px;
    box-sizing: border-box;
    height: 150px;
    width: 150px;
    border-radius: 200px;
    align-self: center;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    padding: 10px 10px 10px 10px;
}
.avatarUploadIcon {
    width: 20px;
    height: 20px;
}
.avatarUploadText {
    align-self: center;
    text-align: center;
    font-family: var(--app-text-main-font);
    font-size: 10px;
    color: lightgray;
}
.avatarAcceptedFormatsText {
    font-size: 12px;
    margin: 0;
    padding: 0;
    color: var(--app-color-gray-light_nuance2);
}
.avatarUploadPictureContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.avatarUploadPictureText {
    align-self: center;
    font-weight: 500;
    text-align: center;
    text-align: center;
    font-family: var(--app-text-main-font);
    font-size: 16px;
    color: var(--app-color-gray-selected);
}
.avatarDragInput {
    display: none;
    align-content: center;
}
.avatarContentContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.avatarUrlImg {
    object-fit: contain;    
    height: 150px;
    width: 150px;
    border-radius: 50%;
    // transform: scale(0.95);
    padding: 2px;
}
.avatarErrorText {
    margin: 0;
    text-align: center;
    color: var(--app-color-error);
    font-family: var(--app-text-main-font);
    font-size: 0.8rem;
    font-weight: 400;
    letter-spacing: 0;
    line-height: 0.8rem;
    white-space: pre-wrap;
}
.labeledFieldHost {
    margin: 0;
    align-self: flex-start;
}
.labeledFieldLabel {
    font-family: var(--app-text-main-font);
    font-size: 16px;
    color: var(--app-color-gray-light_nuance2);
    font-weight: bold;
    text-align: left;
}
.labeledFieldChildrenContainer {
    font-family: var(--app-text-main-font);
    font-size: 16px;
    color: var(--app-color-gray-dark);
    border-radius: 2px;
    border: 1px solid var(--app-color-gray-lighter);
    width: 100%;
    outline: none;
    min-height: 56px;
    margin-top: 5px;
    margin-left: 0;
    padding: 10px 10px;
    display: flex;
    align-items: center;
    flex-direction: column;
    white-space: pre;
    overflow-wrap: break-word;
    word-break: break-all;
    hyphens: auto; 
}
.labeledFieldError {
    margin: 0;
    text-align: left;
    color: var(--app-color-error);
    font-family: var(--app-text-main-font);
    font-size: 0.8rem;
    font-weight: 400;
    letter-spacing: 0;
    line-height: 1rem;
    white-space: pre-wrap;
}
.label {
    font-family: var(--app-text-main-font);
    font-size: 16px;
    color: var(--app-color-gray-light_nuance2);
    font-weight: 500;
}
.inputField {
    font-family: var(--app-text-main-font);
    font-size: 16px;
    color: var(--app-color-gray-dark);
    border-radius: 2px;
    border: 1px solid var(--app-color-gray-lighter);
    width: 100%;
    outline: none;
    height: 56px;
    margin-top: 5px;
    margin-left: 0;
    padding: 0 10px 0 10px;
    text-align: left;
    &[readonly] {
        cursor: default;
    }
    &:-webkit-autofill,
    &:-webkit-autofill:hover,
    &:-webkit-autofill:focus {
        border: 1px solid var(--app-color-gray-lighter);
        font-family: var(--app-text-main-font);
        font-size: 16px;
        color: var(--app-color-gray-dark) !important;
    }
}
.emailParagraph {
    font-family: var(--app-text-main-font);
    font-size: 16px;
    color: var(--app-color-gray-dark);
    width: 100%;
    outline: none;
    margin: 0;
    padding: 5px 0 0;
    line-height: 1.5em;
    word-wrap: break-word;
    white-space: pre-wrap;
    word-break: break-word;
    text-align: left;
}
.positionsParagraph {
    font-family: var(--app-text-main-font);
    font-size: 16px;
    color: var(--app-color-gray-dark);
    width: 100%;
    outline: none;
    margin: 0;
    padding: 5px 0 0;
    line-height: 1.5em;
    word-wrap: break-word;
    white-space: pre-wrap;
    word-break: break-word;
    text-align: left;
}
.errorGeneralText {
    margin: 10px 0 0 0;
    text-align: left;
    color: var(--app-color-error);
    font-family: var(--app-text-main-font);
    font-size: 0.8rem;
    font-weight: 400;
    letter-spacing: 0;
    line-height: 0.8rem;
}
.success {
    margin: 10px 0 0 0;
    text-align: left;
    color: green;
    font-family: var(--app-text-main-font);
    font-size: 0.8rem;
    font-weight: 400;
    letter-spacing: 0;
    line-height: 0.8rem;
}
.userDataWrapper {        
    @media (--app-viewport-medium-max) {
        flex-direction: column;
    }
}
.userSettingsContainer {
    padding: 30px 0 30px 0;
}

.deletePositionContainer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.deletePosition_title {
    height: 40px;
    width: 217px;
    color: var(--app-color-gray-dark);
    font-family: var(--app-text-main-font);
    font-size: 32px;
    font-weight: 500;
    letter-spacing: 0;
    line-height: 40px;
}
.deletePosition_description {
    align-self: flex-start;
    height: 20px;
    color: var(--app-color-gray-light);
    font-family: var(--app-text-main-font);
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 0;
    line-height: 20px;
}
.deletePosition_checkboxContainer {
    align-self: flex-start;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin: 10px 0 10px 0;
}
.deletePosition_buttonContainer {
    svg {
        margin-left: 10px;
        padding: 1px 1px;
        width: 24px;
        height: 24px;
        color: var(--app-color-white-lighter);
    }
}

