.closeIconContainer {
  position: absolute;
  right: 24px;
}

.imageContainer {
  cursor: pointer;
  background-color: transparent;
  border: 0;
  img{
    width: 48px;
    height: 48px;
  }
}
.topContainer{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
}
.avatarContainer{
  margin-top: 60px;
  height: 80px;
  width: 80px;
  background-color: var(--app-color-gray-lighter);
  border-radius: 50%;
  img{
    height: 80px;
    width: 80px;
    border-radius: 50%;
  }
}
.name{
  font-family: var(--app-text-main-font);
  font-size: 1.5rem;
  color: var(--app-color-gray-dark);
  text-align: center;
}
.position{
  font-family: var(--app-text-main-font);
  font-size: 1rem;
  line-height: 1.5rem;
  color: var(--app-color-gray-light);
  text-align: center;
}